(function() {
  'use strict';
  /**
   * @ngdoc overview
   * @name tamedia
   * @description
   * Main module of the tamedia application.
   */
  angular
    .module('tamedia', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ui.bootstrap', 'toastr']);
})();

(function() {
    'use strict';
    /**
     * @ngdoc service
     * @name tamedia.factory:apiFactory
     *
     * @description
     * POST method of the Account API
     *
     * @requires $http
     * @requires apiEndpoint
     *
     */
    angular.module('tamedia')
        .factory('apiFactory', apiFactory);

      function apiFactory(
        $http, apiEndpoint
      ){
        var apiFactoryFactory = {
            // GET Methods
            getProductList: getProductList,
            getSingleProduct: getSingleProduct
        };

        return apiFactoryFactory;

        /**
         * @ngdoc
         * @name tamedia.factory:apiFactory#getProductList
         * @methodOf tamedia.factory:apiFactory
         *
         * @description
         * GET method of the Account API
         *
         * @param {object} paramsObject Accepts params object in this format:
         * <pre>
         * params:{
         *  nbArticles: 20
         * }
         * </pre>
        */
        function getProductList(params) {
          return $http.get(apiEndpoint.proxy + '/articles', params);
        }

        /**
         * @ngdoc
         * @name tamedia.factory:apiFactory#getSingleProduct
         * @methodOf tamedia.factory:apiFactory
         *
         * @description
         * GET method of the Account API, get single product details
         *
         * @param {object} paramsObject Accepts params object in this format:
         * <pre>
         * params:{
         *  url: "https://www.ricardo.ch/kaufen/haushalt-und-wohnen/badezimmer/wc/automatischer-wc-sitz-mit/v/an855739648/"
         * }
         * </pre>
        */
        function getSingleProduct(params) {
          return $http.get(apiEndpoint.proxy + '/getSingleArticle', params);
        }

    }
}());

(function() {
    'use strict';
    /**
     * @ngdoc service
     * @name tamedia.factory:storage
     *
     * @description
     * Works with a localstorage
     *
     *
     */
    angular.module('tamedia')
        .factory('Storage', Storage);

      function Storage(
        $window
      ){
        var storageObject = {
          // Declare all possible storage objects (they are used for bootstrapping)
          items: null
        };
        var prefix = 'tamedia-';
        var localStorageSupported = checkLocalStorage();

        var storageFactory = {
            save: save,
            remove: remove,
            get: get,
            bootstrap: bootstrap
        };

        return storageFactory;

        function remove(key) {
            if (localStorageSupported) {
                $window.localStorage.removeItem(prefix + key);
            }
            // Don't delete keys since they might be needed for bootstrap
            storageObject[key] = null;
        }

        function get(key, bootstrap) {
          // Only return from localStorage during bootstrap
          // Use in-memory storage instead
          if (bootstrap) {
              if (localStorageSupported) {
                  return angular.fromJson($window.localStorage.getItem(prefix + key));
              }
          }
          return storageObject[key];
        }

        function checkLocalStorage() {
            try {
                $window.localStorage.setItem('tamedia.checkLocalStorage', '1');
                $window.localStorage.removeItem('tamedia.checkLocalStorage');
                return true;
            } catch (e) {
                return false;
            }
        }

        function bootstrap() {
            for (var key in storageObject) {
                if (storageObject.hasOwnProperty(key)) {
                    var value = get(key, true);
                    storageObject[key] = value;
                }
            }
        }

        function save(key, value, noPersistency) {
            storageObject[key] = value;
            if(noPersistency){
                // Use noPersistency if object should only be saved for
                // the duration of the session
                return;
            }
            if (localStorageSupported) {
                $window.localStorage.setItem(prefix + key, angular.toJson(value));
        }
      }
    }
}());

(function() {
  'use strict';

  angular
    .module('tamedia')
    .directive('singleProduct', SingleProduct);

  /** @ngInject */
  function SingleProduct() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/single_product/single.product.html',
      scope: {
          product: '=product'
      },
      controller: SingleProductController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function SingleProductController() {

    }
  }

})();

(function() {
  'use strict';

  angular
    .module('tamedia')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope:{
        title: '=',
        backButton: '=',
        backCallback: '='
      }
    };

    return directive;
  }

})();

(function() {
  'use strict';

  /**
   * @ngdoc service
   * @name tamedia.controller:MainController
   *
   * @description
   * Home view controller
   *
   * @requires productList
   * @requires apiFactory
   */

  angular
    .module('tamedia')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController(productList, apiFactory) {
    var vm = this;
    vm.pageTitle = "Latest products";
    vm.productList = productList.data.articles;

    vm.showSingleProd = function(item){
      var config = {
        params: {
          url: item.Url
        }
      };

      vm.singleProductOpen = true;
      vm.pageTitle = "Product overview"

      apiFactory.getSingleProduct(config);
    }

    vm.backCallback = function(){
      vm.singleProductOpen = false;
      vm.pageTitle = "Latest products";
    }
  }
})();

(function() {
  'use strict';

  /**
  * @ngdoc overview
  * @name tamedia.run
  *
  * @description
  * Front-end application run block.
  * Runs only once, right after application bootstrap!
  */

  angular
    .module('tamedia')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope, Storage) {
    Storage.bootstrap();

    var scope = $rootScope;
    scope.$on('$stateChangeStart',function(){
      scope.stateIsLoading = true;
    });

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';
  /**
   * @ngdoc overview
   * @name router
   *
   * @description
   * Application routes
   *
   */

  angular
    .module('tamedia')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      /**
       * @ngdoc overview
       * @name tamedia.route:home
       *
       * @description
       * Home screen route, showing product list
       *
       * @requires tamedia.factory:apiFactory
       * @requires tamedia.controller:MainController
       * @requires tamedia.factory:Storage
       */
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main',
        resolve: {
          productList: /** @ngInject */
            function (apiFactory, Storage) {
              var config = {
                params:{
                  nbArticles: 20
                }
              };

              return apiFactory.getProductList(config).then(function(res){
                Storage.save('items', res.data.articles);
                return res;
              });
            }
        }
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global moment:false */
(function() {
  'use strict';

  /**
   * @ngdoc configuration
   * @name tamedia.config
   * @methodOf tamedia.config
   *
   * @description
   * tamedia application constans
   *
   */

  angular
    .module('tamedia')
      .constant('moment', moment)
      .constant('apiEndpoint', {
          host: 'https://www.ricardo.ch',
          proxy: 'http://localhost:8090'
      })
})();

(function() {
  'use strict';

  /**
   * @ngdoc configuration
   * @name tamedia.config
   * @methodOf tamedia.config
   *
   * @description
   * tamedia application configuration
   *
   */
  angular
    .module('tamedia')
    .config(config);

  /** @ngInject */
  function config($logProvider) {
    // Enable log
    $logProvider.debugEnabled(true);
  }

})();

